export default {
	data() {
		return {
			addrList: [],
			modalTitle: '新增收货地址',
			region: ['', '', '请选择'],
			customItem: '全部',
			showAddrModal: false,
			addrVO: {},
			type: 'add',
			name: '',
			mobilePhone: '',
			address: ''
		};
	},
	onLoad(options) {},
	onShow() {
		this.addrList = []
		this.getList()
	},
	methods: {
		getList() {
			let self = this
			self.$acFrame.HttpService.addrList().then(res => {
				if (res.success) {
					let addrList = res.data
					addrList.filter(v=>{
						if(v.flagDft==1){
							v.flagDft = true
						}else{
							v.flagDft=false
						}
						if(v.flagTop ==1){
							v.flagTop  = true
						}else{
							v.flagTop =false
						}
					})
					self.addrList = addrList
				}
			})
		},
		editAddr(ind) {
			this.type = 'edit'
			this.modalTitle = '编辑收货地址'
			let addrVO = this.addrList[ind];
			this.addrVO = addrVO;
			this.name = addrVO.name;
			this.mobilePhone = addrVO.mobilePhone;
			this.address = addrVO.address;
			console.log(addrVO);
			this.region = [addrVO.areaProvince, addrVO.areaCity, addrVO.areaCounty];
			this.showAddrModal = true;
		},
		deleteAddr(ind) {
			let self = this
			let params = {
				id: this.addrVO.id
			}
			uni.showModal({
				title: '提示',
				content: '是否删除改收货地址？',
				success: function(res) {
					if (res.confirm) {
						self.$acFrame.HttpService.deleteAddr(params).then(res => {
							if (res.success) {
								self.getList()
							}
						})
					} else if (res.cancel) {

					}
				}
			});

		},
		changeVal(e, name) {
			this.addrVO[name] = e.detail.value
		},
		save() {
			let self = this;
			let params = this.addrVO;
			params["name"] = this.name;
			params["mobilePhone"] = this.mobilePhone;
			params["address"] = this.address;
			params["flagTop"] = 1;
			if (this.type == 'add') {
				self.$acFrame.HttpService.addAddr(params).then(res => {
					if (res.success) {
						self.$acFrame.Util.mytotal('新增成功')
						self.getList()
						self.showAddrModal = false;
					}
				})
			} else {

				self.$acFrame.HttpService.deitAddr(params).then(res => {
					if (res.success) {
						self.$acFrame.Util.mytotal('编辑成功');
						self.getList();
						self.showAddrModal = false;
					}
				})
			}

		},
		changeDefault(ind) {
			let item = this.addrList[ind]
			this.addrList.filter((v, i) => {
				if (i == ind) {
					v.flagDft = true
				} else {
					v.flagDft = false
				}
			})
			this.setAddr(item.id,'default');

		},
		setAddr(id,type){
			let self = this
			let text=''
			let params = {
				isDft:false,
				isTop:false,
				id:id
			}
			if(type=='top'){
				params.isTop=true
				text='置顶成功'
			} else{
				params.isDft=true
				text='设置默认地址成功'
			}
			self.$acFrame.HttpService.setAddr(params).then(res => {
				if (res.success) {
					self.getList()
				}
			})
		},
		bindAreaChange(e) {
			let vals = e.detail.value
			this.region = vals
			this.addrVO.areaProvince = vals[0]
			this.addrVO.areaCity = vals[1]
			this.addrVO.areaCounty = vals[2]
		},
		showModal() {
			this.showAddrModal = true
			this.modalTitle = '新增收货地址'
			this.type = 'add'
			this.addrVO = {},
				this.name = '';
			this.mobilePhone = '';
			this.address = '';
			this.region = ['', '', '请选择']
		},
		closeModal() {
			this.showAddrModal = false
		},
		chooseAddr(ind) {
			let pages = getCurrentPages()
			let addrVO = this.addrList[ind]
			if (pages.length > 1) {
				let prePage = pages[pages.length - 2]
				if (prePage.$vm.setAddr) {
					prePage.$vm.setAddr(addrVO)
					wx.navigateBack({})
				}
			}
		}
	}
}
